export default {
	data() {
		return {
			indexActiveImg: this.$indexActiveImg,
			videoImg: this.$videoImg,
			myImg: this.$myImg,
			downAppImg: this.$downAppImg,

			top: 0,
			token: '',
			displayNone: false,
			topMenuPosition: false,
			refreshing: false,
			downAppHeight: 0,
			providerList: [],
			list: [],
			fetchPageNum: 1,
			allPhoto: {
				id: "",
				photoList: []
			},
		}
	},
	onLoad() {
		this.getData();
		let self = this;
		uni.getStorage({
			key: 'user_info',
			success: function(res) {
				self.token = res.data.token
			}
		})
		// 	uni.getProvider({
		// 		service: 'share',
		// 		success: (e) => {
		// 			let data = []
		// 			for (let i = 0; i < e.provider.length; i++) {
		// 				switch (e.provider[i]) {
		// 					case 'weixin':
		// 						data.push({
		// 							name: '分享到微信好友',
		// 							id: 'weixin'
		// 						})
		// 						data.push({
		// 							name: '分享到微信朋友圈',
		// 							id: 'weixin',
		// 							type: 'WXSenceTimeline'
		// 						})
		// 						break;
		// 					case 'qq':
		// 						data.push({
		// 							name: '分享到QQ',
		// 							id: 'qq'
		// 						})
		// 						break;
		// 					default:
		// 						break;
		// 				}
		// 			}
		// 			this.providerList = data;
		// 		},
		// 		fail: (e) => {
		// 			console.log('获取分享通道失败', e);
		// 		}
		// 	});
	},
	onPageScroll(e) {
		if (e.scrollTop > this.top && e.scrollTop > 20) {
			this.displayNone = true;
		} else {
			this.displayNone = false;
		}
		if (e.scrollTop > 0) {
			this.topMenuPosition = true;
		} else {
			this.displayNone = false;
			this.topMenuPosition = false;
		}
		this.top = e.scrollTop;
	},
	// onReachBottom() {
	// 	console.log('滑动到页面底部')
	// 	this.getData();
	// },
	onPullDownRefresh() {
		console.log('下拉刷新');
		this.refreshing = true;
		this.getData();
	},
	methods: {
		gotoLogin() {
			console.log(this.token)
			if (this.token) {
				uni.redirectTo({
					url: '../../pages/center/center'
				})
			} else {
				uni.redirectTo({
					url: '../../pages/login/login'
				})
			}
		},
		getData() {
			this.topMenuPosition = true;
			uni.request({
				url: this.$serverUrl + '/blog/hot',
				data: {
					pageNum: 1,
					pageSize: 10,
				},
				header: {
					// "Content-Type": "application/json;charset=UTF-8",
					"Content-Type": "application/x-www-form-urlencoded"
				},
				method: "POST",
				success: (ret) => {
					if (ret.statusCode !== 200) {
						console.log('失败!');
						this.refreshing = false;
						uni.stopPullDownRefresh();
					} else {
						console.log(ret.data.data)
						this.topMenuPosition = false;
						if (this.refreshing && ret.data.data[0].id === this.list[0].id) {
							uni.showToast({
								title: '已经最新',
								icon: 'none',
							})
							this.refreshing = false;
							uni.stopPullDownRefresh();
							return;
						}
						if (this.refreshing) {
							this.refreshing = false;
							uni.stopPullDownRefresh()
							this.list = ret.data.data;
							this.fetchPageNum = 2;
						} else {
							this.list = ret.data.data;
							this.fetchPageNum += 1;
						}
					}
				},
				fail: (ret) => {
					this.refreshing = false;
					uni.stopPullDownRefresh();
				}
			});
		},
		goDetail(item) {
			console.log(item.id)
			uni.navigateTo({
				url: '../../pages/particulars/particulars?id=' + item.id
			});







			// uni.navigateTo({
			// 	url: '../detail/detail?data=' + encodeURIComponent(JSON.stringify(e))
			// })
		},
		goFollow() {
			uni.redirectTo({
				url: '../../pages/new/new'
			})
		},
		goPost() {
			uni.redirectTo({
				url: '../../pages/new/post'
			})
		},
		share(e) {
			if (this.providerList.length === 0) {
				uni.showModal({
					title: '当前环境无分享渠道!',
					showCancel: false
				})
				return;
			}
			let itemList = this.providerList.map(function(value) {
				return value.name
			})
			uni.showActionSheet({
				itemList: itemList,
				success: (res) => {
					uni.share({
						provider: this.providerList[res.tapIndex].id,
						scene: this.providerList[res.tapIndex].type && this.providerList[res.tapIndex].type === 'WXSenceTimeline' ?
							'WXSenceTimeline' : 'WXSceneSession',
						type: 0,
						title: 'uni-app模版',
						summary: e.title,
						imageUrl: e.img_src,
						href: 'https://uniapp.dcloud.io',
						success: (res) => {
							console.log('success:' + JSON.stringify(res));
						},
						fail: (e) => {
							uni.showModal({
								content: e.errMsg,
								showCancel: false
							})
						}
					});
				}
			})
		},
		onConcern(idx) {
			let id = 'follow' + idx;
			if (this.token) {
				if (document.getElementById(id).innerText === '关注') {
					uni.showToast({
						title: '关注成功',
						icon: 'none',
					})
					document.getElementById(id).innerText = '取消关注'
				} else {
					uni.showModal({
						title: '',
						content: '确定不再关注？',
						showCancel: true,
						cancelText: '取消',
						cancelColor: '#9C9C9C',
						confirmText: true,
						confirmText: '确定',
						confirmColor: '#E96900',
						success: function(res) {
							if (res.confirm) {
								console.log('用户点击确定');
								uni.showToast({
									title: '已取消关注',
									icon: 'none',
								})
								document.getElementById(id).innerText = '关注'
							} else if (res.cancel) {
								console.log('用户点击取消');
							}
						}
					})
				}
			} else {
				uni.showModal({
					title: '尚未登录，无法关注',
					content: '请问是否跳转登录页面',
					showCancel: true,
					cancelText: '稍后登录',
					cancelColor: '#9C9C9C',
					confirmText: true,
					confirmText: '现在登录',
					success: function(res) {
						if (res.confirm) {
							uni.redirectTo({
								url: '../../pages/login/login?url=' + '/new/recommend'
							})
						} else if (res.cancel) {
							console.log('用户点击取消');
						}
					}
				})
			}
		},
		gotoVideo() {
			uni.redirectTo({
				url: '../../pages/video/video?url=/new/recommend'
			})
		}
	}
}
